                    index: '/productcategory',
                    title: '商品分类管理'
                },
                {
                    icon: 'el-icon-lx-copy',
                    index: '/brands',
                    title: '商品品牌管理'
                },
              
                {
                    icon: 'el-icon-lx-emoji',
                    index: '/productattributecategory',
                    title: '商品属性分类管理'
                },
                {
                    icon: 'el-icon-pie-chart',
                    index: '/productcreate',
                    title: '商品管理'
                },
                // {
                //     icon: 'el-icon-rank',
                //     index: '6',
                //     title: '拖拽组件',
                //     subs: [
                //         {
                //             index: 'drag',
                //             title: '拖拽列表'
                //         },
                //         {
                //             index: 'dialog',
                //             title: '拖拽弹框'
                //         }
                //     ]
                // },
                // {
                //     icon: 'el-icon-lx-global',
                //     index: 'i18n',
                //     title: '国际化功能'
                // },
                {
                    icon: 'el-icon-lx-warn',
                    index: '7',
                    title: '商品评论管理',
                   
                },
                 {
                    icon: 'el-icon-lx-redpacket_fill',
                     index: '/',
                     title: '商品订单管理'
                 },
                 {
                    icon: 'el-icon-lx-redpacket_fill',
                     index: '/test',
                     title: '商品添加测试'
                 }
            ]
        };
    },
    computed: {
        onRoutes() {
            return this.$route.path.replace('/', '');
        }
    },
  
};
